@import url('https://fonts.googleapis.com/css2?family=MuseoModerno:wght@300;400;600&display=swap');;

:root{
  --primaryColor: #E2EdEb;
  --secondaryColor: #ffffff;
  --dropColor: #B8dbde;
  --borderRadius10: 10px;
  --fontFamily: 'MuseoModerno', cursive;
  --fontWeight300: 300;
  --fontWeight400: 400;
  --fontWeight600: 600;
  --fontSize14: 14px;
  --centerAlign: center;
  --pointer: pointer;
}


body{
  background-color: var(--primaryColor);
}

.App {
  text-align: var(--centerAlign);
  padding: 2% 5%;
}

.App * {
  box-sizing: border-box;
}

ul {
  padding: 0;
  list-style: none;
}

h2{
  font-family: var(--fontFamily);
  font-weight: var(--fontWeight600);
}

p {
  font-family: var(--fontFamily);
  font-weight: var(--fontWeight400);
  margin: 5px 0;
}

input[type="text"],
input[type="number"] {
  padding: 8px 6px 8px 10px;
  outline: none;
  border-radius: var(--borderRadius10);
  border: 1px solid var(--primaryColor);
  font-family: var(--fontFamily);
  font-weight: var(--fontWeight300);
  font-size: var(--fontSize14);
  line-height: 20px;
  align-self: var(--centerAlign);
  height: 38px;
  margin-bottom: 20px;
}

.city {
  width: 250px;
}

#city-search {
  display: flex;
  flex-direction: column;
  margin: auto;
  position: relative;
}

.suggestions {
  width: 250px;
  position: absolute;
  top: 45px;
  z-index: 2;
  align-self: var(--centerAlign);
  margin: 0;
  font-size: var(--fontSize14);
  box-shadow: 10px 10px 16px -12px rgba(0, 0, 0, 0.75);
}

.suggestions > li {
  text-align: left;
  padding: 8px 6px 8px 10px;
  background-color: var(--dropColor);
  cursor: default;
}

.suggestions > li:last-child {
  border-bottom-left-radius: 4px;
  border-bottom-right-radius: 4px;
}

.suggestions > li:hover {
  background-color: var(--dropColor);
}

.event {
  padding: 10px 10px 35px;
  margin-bottom: 10px;
  text-align: left;
  background-color: var(--secondaryColor);
  border: 1px solid var(--primaryColor);
  border-radius: var(--borderRadius10);
  position: relative;
}

.details-btn {
  position: absolute;
  right: 5px;
  bottom: 5px;
  padding: 5px 8px;
  border-radius: var(--borderRadius10);
  background-color: var(--dropColor);
  outline: none;
  cursor: var(--pointer);
}

.alerts-container {
  position: fixed;
  top: 0px;
  left: 20px;
  width: 250px;
  z-index: 1000;
}

.charts-container {
  display: grid;
  grid-template-columns: 1fr 1fr;
}

@media (max-width: 768px){
  .charts-container{
    grid-template-columns: 1fr;
  }
  .recharts-responsive-container:first-of-type{
    margin-left: 5%;
  }
  #event-list{
    grid-template-columns: 1fr 1fr;
  }
}

@media (max-width: 480px){
  #event-list{
    grid-template-columns: 1fr;
  }
}